import { Component, OnInit } from '@angular/core';
import { News } from 'src/app/models/news.model';
import { CookieCatcherService } from 'src/app/services/cookie-catcher.service';
import { NewsService } from 'src/app/services/news.service';

import { typeData } from '../typeData';

@Component({
    selector: 'app-news-add',
    templateUrl: './news-add.component.html',
    styleUrls: ['./news-add.component.css']
})

export class NewsAddComponent implements OnInit {
    news: News = new News();
    submitted = false;
    typeData = typeData;

    constructor(
        private NewsService: NewsService,
        public CookieCatcherService: CookieCatcherService
    ){ }

    ngOnInit(): void {
    }

    saveNews(): void
    {
        this.news.created_date = String(new Date());
        this.news.updated_date = String(new Date());
        
        this.NewsService.create(this.news)
            .then( () => {
                this.submitted = true;
            })
    }
}
