import { Component, OnInit } from '@angular/core';
import { NewsService } from 'src/app/services/news.service';
import { News } from 'src/app/models/news.model';
import { map } from 'rxjs/operators';

// 靜態資源
import { path } from 'src/environments/path.map';
import { CookieCatcherService } from 'src/app/services/cookie-catcher.service';

@Component({
    selector: 'app-news-view',
    templateUrl: './news-view.component.html',
    styleUrls: ['./news-view.component.css']
})

export class NewsViewComponent implements OnInit {
    newsData?: News[];
    currentNews?: News;
    currentIndex = -1;
    title = '';

    pageLength: number = 0;
    pageIndex: number = 0;
    pageMax: number = 0;

    constructor(
        private newsService: NewsService,
        public CookieCatcherService: CookieCatcherService
    ) {
        // console.log(this.CookieCatcherService.userStatus);
    }

    ngOnInit(): void 
    {
        this.retrieveNews();
    }

    refreshList(): void
    {
        this.currentNews = undefined;
        this.currentIndex = -1;
        this.retrieveNews();
    }

    retrieveNews(): void
    {
        this.newsService.getAll()
            .snapshotChanges()
            .pipe(
                map(changes => 
                    changes.map(c =>
                        ({ key: c.payload.key, ...c.payload.val() })
                    )
                )
            ).subscribe(data => {
                this.newsData = data;
                this.newsData.reverse();

                this.pageLength = this.newsData.length;
                this.newsData = this.newsData.slice(this.pageIndex*10, (this.pageIndex+1)*10);
                this.pageMax = Math.floor((this.pageLength-1) / 10) + 1;

                this.currentNews = this.newsData[0];
                this.currentIndex = 0;
            });
    }

    setActiveNews(news: News, index: number): void
    {
        this.currentNews = news;
        this.currentIndex = index;
    }

    removeAllNews(): void
    {
        this.newsService.deleteAll()
            .then( () => this.refreshList() )
            .catch(err => console.log(err));
    }

    classTables(type: string): any
    {
        if(type === 'main'){
            return (this.currentNews === null)?'col-7':'col-7';
        }
        if(type === 'child'){
            return (this.currentNews === null)?'col-5':'col-5';
        }
    }

    changePages(page: boolean): void
    {
        if(page){
            if((this.pageIndex + 1)*10 < this.pageLength)
            {
                this.pageIndex += 1;
            }
        }
        if(!page){
            if((this.pageIndex - 1)*10 >= 0)
            {
                this.pageIndex -= 1;
            }
        }
        this.retrieveNews();
    }
}
