import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, RouterStateSnapshot, UrlTree } from '@angular/router';
import { CookieService } from 'ngx-cookie-service';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { User } from '../models/user.model';
import { CookieCatcherService } from '../services/cookie-catcher.service';
import { UserService } from '../services/user.service';

@Injectable({
    providedIn: 'root'
})

export class AuthGuard implements CanActivate {
    constructor(
        private UserService: UserService,
        public CookieCatcherService: CookieCatcherService
    ){ }

    canActivate(
        route: ActivatedRouteSnapshot,
        state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree 
    {
        if(!this.CookieCatcherService.checkUserRoot())
        {
            window.alert('您沒有訪問該路徑的權限');
        }
        
        return this.CookieCatcherService.checkUserRoot();
    }
}
